var app = getApp().globalData
export default {
	data() {
		return {
			detail: {},
			content:null,
			comments:[],
			thought:'',
			id:'',
			finish:false
		}
	},
	onShareAppMessage:function(res) {
		return {
		  title: this.detail.title?this.detail.title.substring(0,30)+'...':'学院详情',
		  imageUrl:this.detail.cover?this.detail.cover:app.shareImg,
		  path: '/pagesTwo/SchoolDetail/SchoolDetail?id='+this.id
		}
	},
	onLoad(e) {
		// console.log(e.id)
		this.id = e.id
		this.getData(e.id)
		this.getCom(e.id)
	},
	methods: {
		// 跳转个人主页
		MyInfo(id){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'../../pagesMy/MyInfo/MyInfo?id='+id
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
		},
		// 发表评论
		send () {
			if(uni.getStorageSync('userId')){
				if (this.thought.length && this.thought.trim().length) {
				  this.$api.g_s_comment({
				   token: uni.getStorageSync("token"),
				   user_id: uni.getStorageSync("userId"),
				   id: this.id,
				   content: this.thought
				  }).then(res => {
				    if (res.data.code === '200') {
				      uni.showToast({
				      		title: res.data.msg,
				      		duration: 2000,
				      		icon:'none'
				      });
				      this.thought = ''
				      this.getCom()
						this.getData()
						setTimeout(function() {
							uni.pageScrollTo({
								scrollTop:1000000
							})
						}, 300);
										
				    } else if (res.data.code === '1073') {
				      uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:'none'
				      });
				    }
				  })
				  // }
				} else {
				   uni.showToast({
						title: '评论内容不可为空',
						duration: 2000,
						icon:'none'
				   });
				}
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
		      
		},
		// 评论列表
		getCom(id){
			this.$api.grow_school_comments({
				user_id:uni.getStorageSync('userId')||'',
				token: uni.getStorageSync('token')||'',
				id:this.id
			}).then(res=>{
				if (res.data.code === '200') {
					// console.log(res)
					  this.comments = res.data.data
					 
					} else if (res.data.code === '1010') {
					  uni.clearStorageSync()
					  uni.showToast({
					      title: '登录过期，请重新登录',
					      duration: 2000,
						  icon:'none'
					  });
					  uni.reLaunch({
					  	url: '../../pages/login/login'
					  })
					}
			})
		},
		getData(id){
			this.$api.grow_school_detail({
				user_id:uni.getStorageSync('userId')||'',
				token: uni.getStorageSync('token')||'',
				id:this.id
			}).then(res=>{
				if (res.data.code === '200') {
					console.log(res)
					  this.detail = res.data.data
					  
					} else if (res.data.code === '1010') {
					  uni.clearStorageSync()
					  uni.showToast({
					      title: '登录过期，请重新登录',
					      duration: 2000,
						  icon:'none'
					  });
					  uni.reLaunch({
					  	url: '../../pages/login/login'
					  })
					}
				this.$nextTick(()=>{
					this.finish = true
				})
			})
		}
	},

	
}
